import Box from "../UI/Box";

import style from "./About.module.css";
import whiteMusicalNote from "../../images/white_musical_note.png"

const About = () => {
  return (
    <div className={style.about}>
      <div className={style.about_box}>
        <Box title="About" dropdown={false} icon={whiteMusicalNote}>
          Since people usually put their lyre notes using PC Notes (i.e.
          keyboard’s key), i made this converter for mobile and PS player. You
          can easily convert those PC Notes into the format that suits you so
          that you can also play or learn the song that you want easily. I hope
          this is helpful! Any suggestion or feedback are welcome :D
        </Box>
      </div>
    </div>
  );
};

export default About;
