import { Fragment, useState, useEffect } from "react";
import About from "./components/Content/About";
import PCNotes from "./components/Content/PCNotes";
import ConverterNotes from "./components/Content/ConverterNotes";
import Help from "./components/Content/Help";
import Credit from "./components/Content/Credit";
import NavBar from "./components/Navigation/NavBar";

import {
  mobileNoteMapping,
  musicalNoteMapping,
  PSNoteMapping,
} from "./components/ConverterMapping/ConverterMapping";

import style from "./App.module.css";
import venti from "./venti_cropped_2.svg";

const App = () => {
  const [inputText, setInputText] = useState(``);
  const [convertedText, setConvertedText] = useState("");
  const [colorConvertedText, setColoredConvertedText] = useState([]);
  const [mappingType, setMappingType] = useState(mobileNoteMapping);
  const [isSpace, setIsSpace] = useState(false);
  const [isColor, setIsColor] = useState(false);
  const [isNumber, setIsNumber] = useState(true);

  const converter = (noteText) => {
    setInputText(noteText);
    setConvertedText("");
    setColoredConvertedText([]);

    for (var i = 0; i < noteText.length; i++) {
      const key = noteText.charAt(i);
      const convertedNote = mappingType[key.toLowerCase()];

      if (convertedNote !== undefined) {
        if (isColor) {
          convertWithColor(convertedNote, true);
        } else if (!isNumber) {
          removeNumber(convertedNote);
        } else {
          setConvertedText((prevState) => {
            return prevState + convertedNote + (isSpace ? " " : "");
          });
        }
      } else {
        if (isColor) {
          convertWithColor(key, false);
        } else {
          setConvertedText((prevState) => {
            return prevState + key + (isSpace ? " " : "");
          });
        }
      }
    }
  };

  const changeMappingHandler = (mappingKey) => {
    if (mappingKey === "Mobile Notes") {
      setMappingType(mobileNoteMapping);
    } else if (mappingKey === "Musical Notes") {
      setMappingType(musicalNoteMapping);
    } else if (mappingKey === "PS Button Notes") {
      setMappingType(PSNoteMapping);
    }
  };

  useEffect(() => {
    converter(inputText);
  }, [mappingType, isSpace, isColor, isNumber]);

  const spacingHandler = (event) => {
    if (event.target.tagName === "LABEL") {
      event.preventDefault();
    }
    setIsSpace(!isSpace);
  };

  const colorHandler = (event) => {
    if (event.target.tagName === "LABEL") {
      event.preventDefault();
    }
    setIsColor(!isColor);
  };

  const convertWithColor = (keyNote, converted) => {
    var spanText;
    if (!converted) {
      spanText = <span>{keyNote + (isSpace ? " " : "")}</span>;
    } else {
      let numberList = [];
      if (mappingType === musicalNoteMapping) {
        numberList = ["3", "4", "5"];
      } else {
        numberList = ["1", "2", "3"];
      }

      if (keyNote.includes(numberList[0])) {
        if (!isNumber) {
          keyNote = keyNote.slice(0,-1);
        }
        spanText = (
          <span className={style.low}>{keyNote + (isSpace ? " " : "")}</span>
        );
      } else if (keyNote.includes(numberList[1])) {
        if (!isNumber) {
          keyNote = keyNote.slice(0,-1);
        }
        spanText = (
          <span className={style.medium}>{keyNote + (isSpace ? " " : "")}</span>
        );
      } else if (keyNote.includes(numberList[2])) {
        if (!isNumber) {
          keyNote = keyNote.slice(0,-1);
        }
        spanText = (
          <span className={style.high}>{keyNote + (isSpace ? " " : "")}</span>
        );
      } else {
        spanText = <span>{keyNote + (isSpace ? " " : "")}</span>;
      }
    }
    setColoredConvertedText((prevState) => {
      return [...prevState, spanText];
    });
  };
  
  const numberHandler = (event) => {
    if (event.target.tagName === "LABEL") {
      event.preventDefault();
    }
    setIsNumber(!isNumber);
    if(convertedText.length > 0 || colorConvertedText.length >0){  
      removeNumber();
    }
  }

  const removeNumber = () => {
    if (!isNumber) {
      setIsColor(true);
    }
  };

  return (
    <Fragment>
      <NavBar />
      <header>
        <div className={style.title_box}>
          <h1 id="about"> Genshin Lyre Converter: PC to Mobile Notes</h1>
          <About />
          <div id="converter" className={style.goto_converter}></div>
        </div>
        <img src={venti} alt="Venti Genshin Impact With Lyre" className={style.venti} />
      </header>
      <section>
        <div className={style.converter}>
          <PCNotes className={style.pc_notes} onConvert={converter} />
          <ConverterNotes
            note={convertedText}
            onChangeMapping={changeMappingHandler}
            onClickSpacing={spacingHandler}
            onClickColor={colorHandler}
            onClickNumber={numberHandler}
            spacingChecked={isSpace}
            colorChecked={isColor}
            colorNote={colorConvertedText}
            numberChecked={isNumber}
          />
        </div>
      </section>
      <footer>
        <div className={style.goto_help} id="help"></div>
        <Help />
        <Credit />
      </footer>
    </Fragment>
  );
};

export default App;
