import style from "./NavBar.module.css";
import { Fragment, useState} from "react";

const NavBar = () => {
  const [isDisplay, setIsDisplay] = useState(false);
  const menuDisplayHandler = () => {
   setIsDisplay(!isDisplay) 
  }
  var displayStyling = isDisplay ? '' : style.remove_display;

  return (
    <Fragment>
      <ul>
        <li><a href="#converter"><i className="fas fa-sync-alt"></i>Converter</a></li>
        <li><a href="#about"><i className="fas fa-info"></i>About</a></li>
        <li><a href="#help"><i className="fas fa-question-circle"></i>Help</a></li>
        <li><a href="https://forms.gle/ueMbbEtdRfpiiv5k9"><i className="fas fa-comment-dots"></i>Feedback</a></li>
      </ul>

      <div className={style.mobile_menu_container} onClick={isDisplay? menuDisplayHandler : undefined}>
        <div className={style.hamburger_menu} onClick={menuDisplayHandler}><i className="fas fa-bars"></i></div>
        <div className={style.mobile_item_container} id={displayStyling}>
          <div className={style.mobile_item}>
            <a href="#converter"><i className="fas fa-sync-alt"></i>Converter</a>
          </div>
          <div className={style.mobile_item}>
            <a href="#about"><i className="fas fa-info"></i>About</a>
          </div>
          <div className={style.mobile_item}>
            <a href="#help"><i className="fas fa-question-circle"></i>Help</a>
          </div>
          <div className={style.mobile_item}>
            <a href="https://forms.gle/ueMbbEtdRfpiiv5k9"><i className="fas fa-comment-dots"></i>Feedback</a>
          </div>
        </div>
      </div>
    </Fragment>
  );
}

export default NavBar;