import style from "./InputBox.module.css";

const InputBox = (props) =>{
  const keyInputHandler = (event) => {
    props.onConvert(event.target.value);
  };

  return (
    <textarea
      id="pc_notes"
      name="pc_notes"
      placeholder={props.placeholder}
      onChange={keyInputHandler}
      autoFocus={true}
    ></textarea>
  );
};

export default InputBox;
