import style from "./Box.module.css";

const Box = (props) => {
  const headerColor = props.headerColor
    ? `${style.box_header} ${style.colored}`
    : `${style.box_header}`;

  let options = []
  if (props.dropdown) {
    for (var i = 0; i < props.options.length; i++) {
      options.push(<option value={props.options[i]} key={i}>{props.options[i]} </option>);
    }
  }

  const mappingChangeHandler = (event) => {
    props.onChangeMapping(event.target.value);
  }

  const moreOptionHandler = (event) => {
    if (event.target.className.includes(style.setting_icon_transform)) {
      event.target.classList.remove(style.setting_icon_transform);
    }
    else {
      event.target.classList.add(style.setting_icon_transform);
    }
    
    props.onMoreOption();
  }

  return (
    <div className={style.box}>
      <div className={headerColor}>
        {props.dropdown && (
          <select name={props.optionID} id={props.optionID} onChange={mappingChangeHandler}>
            {options}
          </select>
          
        )}
        {!props.dropdown && <h2>{props.title}</h2>}
        <img src={props.icon}></img>
        {props.help && <a href="#help" className={style.help_icon}><i className="fas fa-question-circle"></i></a>}
        {props.onMoreOption && <i onClick={moreOptionHandler} className={`fas fa-cog ${style.setting_icon}`}></i>}
      </div>
      <div className={style.box_content}>{props.children}</div>
    </div>
  );
};

export default Box;
