import Box from "../UI/Box";

import style from "./ConverterNotes.module.css";
import { useState } from "react";
import musicalNote from "../../images/musical-note.png";

const ConverterNotes = (props) => {
  const [moreOption, setMoreOption] = useState(false);
  const placeholderText = `Output example: \n do2mi3 sol2 mi3 sol2 mi3 sol2 mi3 fa3 \n ti2mi3 sol2 mi3 sol2 mi3 sol2 do3 re3`;
  const placeholder = <p className={style.placeholder}> {placeholderText} </p>;

  const isNotes = props.note.length !== 0 || props.colorNote.length !== 0;
  var converterNote = props.colorChecked ? props.colorNote : props.note

  const moreOptionHandler = (event) => {
    setMoreOption(!moreOption);
  };
  const moreOptionStyling =
    style.more_option + " " + (moreOption ? style.more_option_display : "");

  return (
    <div className={style.converter_notes}>
      <Box
        dropdown={true}
        options={["Mobile Notes", "Musical Notes", "PS Button Notes"]}
        optionID="converter_notes"
        headerColor={true}
        icon={musicalNote}
        onChangeMapping={props.onChangeMapping}
        help={true}
        onMoreOption={moreOptionHandler}
      >
        <div className={moreOptionStyling}>
          <div onClick={props.onClickSpacing}>
            <input
              type="checkbox"
              id="spacing"
              checked={props.spacingChecked}
              readOnly
            ></input>
            <label htmlFor="spacing">Set space to each notes</label>
          </div>
          <div onClick={props.onClickColor}>
            <input
              type="checkbox"
              id="color"
              checked={props.colorChecked}
              readOnly
            ></input>
            <label htmlFor="color">Set color to row notes</label>
          </div>
          <div onClick={props.onClickNumber}>
            <input
              type="checkbox"
              id="number"
              checked={props.numberChecked}
              readOnly
            ></input>
            <label htmlFor="number">Set row notes number</label>
          </div>
        </div>
        <div className={style.converter_box}>
          {isNotes ? converterNote : placeholder}
        </div>
      </Box>
    </div>
  );
};

export default ConverterNotes;
