import Box from "../UI/Box";
import InputBox from "../UI/InputBox";

import style from "./PCNotes.module.css";
import musicalNote from "../../images/musical-note.png";

const PCNotes = (props) => {
  return (
    <div className={style.pc_notes}>
      <Box
        title="PC Notes"
        dropdown={false}
        headerColor={true}
        icon={musicalNote}
      >
        <InputBox
          placeholder={`Put PC Notes here, e.g.: \nAE G E G E G E R \nME G E G E G Q W`}
          onConvert={props.onConvert}
        ></InputBox>
      </Box>
    </div>
  );
};

export default PCNotes;
